nav{
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: black;
    color: white;
}
nav ul{
    display: flex;
    list-style: none;

}
nav ul li{
    margin-inline: 22px;
    font-size: 22px;
}
.imgs{
    width: 600px;
    justify-content: center;
    flex-wrap: wrap;
    margin: auto;
    position: relative;
    margin-top: 88px;
     
    
}
.card{
    margin-top: 32px;
    display: flex;
    background-color: #d8d8d8;
    padding: 22px;
    border-radius: 11px;
}
.card .text{
    margin-left: 11px;
    font-size: 20px;
}
.imgs img{
    width: 250px;
    height: 200px;

}
.categoryList{
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 22px;
}
.categoryList label{
    font-size: 24px;
}
.categoryList select{
    border: 2px solid black;
    margin-left: 11px;
    padding: 2px;
    font-size: 17px;
}
.searchBar input{
    padding: 4px;
    font-size: 18px;
    border: 2px solid black;
    outline: none;
}